//* Ë°®Ê†ºÊèêÁ§∫Ê°ÜÔºåÈº†Ê†áÊÇ¨ÂÅúÊó∂ÊòæÁ§∫ÁöÑÂÜÖÂÆπ  ‚Äî‚Äî  Ë¥ß‰Ωç‰ø°ÊÅØÔºöË¥ßÂìÅÂêçËØç„ÄÅÁîü‰∫ßÊó•Êúü„ÄÅÂú®Â∫ìÊï¥Êï∞ÊÄªÊï∞
function titleFN(data: string, record?: any, index?: number) {
	// console.log('titleFN', data, record, index)
	if (!data) return null
	const lane = record?.lane ?? ''
	const row = record?.row ?? ''
	const layer = record?.layer ?? ''
	const str = `${row}Êéí - ${layer}Â±Ç - ${data}Âàó`

	let color = data === 'Á©∫Èó≤' ? '#B4EEB4' : '#FF6A6A' // ÁªøËâ≤ / Á∫¢Ëâ≤
	color = '#FFF'

	return (
		<Tooltip
			placement="top"
			color="#fff"
			title={
				<table className="w-full border-collapse  text-slate-700 ">
					<tr>
						<th className="w-[250px] text-[12px] text-center">Ë¥ßÂìÅÂêçËØç</th>
						<th className="w-[150px] text-[12px] text-center">Áîü‰∫ßÊó•Êúü</th>
						<th className="w-[350px] text-[12px] text-center">Âú®Â∫ìÊï¥Êï∞ÊÄªÊï∞</th>
					</tr>
					<tr>
						<td className="  text-[12px] text-center">È£üÂìÅÁÅ´ËÖøËÇ†</td>
						<td className="  text-[12px] text-center">2020-11-19 12:32:00</td>
						<td className="w-[350px] text-[12px] text-center">500ÁÆ±0Ê†π</td>
					</tr>
				</table>
			}>
			{/* {str} */}
			<div style={{ backgroundColor: color, padding: '4px 8px', textAlign: 'center' }}>{str}</div>
		</Tooltip>
	)
}

const statuses = ['Á©∫Èó≤', 'Âç†Áî®']
const randomStatus = statuses[Math.floor(Math.random() * statuses.length)]
console.log(randomStatus) // ÈöèÊú∫ËæìÂá∫ÔºöÁ©∫Èó≤ Êàñ Âç†Áî®
let rawData: any[] = []
for (let w = 1; w < 2; w++) {
	// Â∑∑ÈÅì
	for (let i = 1; i < 3; i++) {
		// Êéí
		for (let j = 1; j < 7; j++) {
			// Âàó
			for (let k = 1; k < 5; k++) {
				// Â±Ç
				const res = `Á¨¨${i}Êéí ${j}Âàó ${k}Â±Ç`
				console.log(res)
				let obj = {}
				if (j == 1) Object.assign(obj, { column1: j })
				if (j == 2) Object.assign(obj, { column2: j })
				if (j == 3) Object.assign(obj, { column3: j })
				if (j == 4) Object.assign(obj, { column4: j })
				if (j == 5) Object.assign(obj, { column5: j })
				if (j == 6) Object.assign(obj, { column6: j })
				rawData.push({
					key: res,
					lane: w, // Â∑∑ÈÅì
					row: i, // Êéí
					column: j, // Âàó
					layer: k, // Â±Ç
					status: randomStatus,
					...obj,
				})
			}
		}
	}
}
// console.log('Ë¥ß‰Ωç rawDataÔºö', rawData)
// !  Ëøô‰∏™Êï∞ÁªÑÔºålaneÁõ∏ÂêåÂêàÂπ∂Ë°åÔºårow‰∏ÄÊ†∑ÂêàÂπ∂Ë°åÔºåËØ•Â¶Ç‰ΩïÂ§ÑÁêÜËøô‰∏™Êï∞ÁªÑÔºü

// üìå 1Ô∏è‚É£ Â§ÑÁêÜÊï∞ÊçÆÔºåÊää column1 ~ columnN ÁªìÊûÑÊï¥ÁêÜÂ•Ω
const groupedData: any[] = []

rawData.forEach(item => {
	const { lane, row, layer, column } = item
	const key = `Á¨¨${lane}Â∑∑ÈÅì - ${row}Êéí - ${layer}Â±Ç`

	let existing = groupedData.find(d => d.key === key)
	if (!existing) {
		existing = {
			key,
			lane,
			row,
			layer,
			column1: null,
			column2: null,
			column3: null,
			column4: null,
			column5: null,
			column6: null,
			// ...item,
		}
		groupedData.push(existing)
	}

	// ÊåâÂàóÂè∑Â°´ÂÖÖ column1 ~ columnN
	existing[`column${column}`] = column
})

console.log('Ë¥ß‰Ωç rawDataÔºö', rawData) //* ÊÄªÂÖ±24Êù°
console.log('ÂàùÂßã groupedData', groupedData) //* ÊÄªÂÖ±8Êù°

// üìå 2Ô∏è‚É£ Â§ÑÁêÜ rowSpanÔºåÂêàÂπ∂Áõ∏ÂêåË°å
const rowSpanMap = new Map<string, number>()
groupedData.forEach((item, index) => {
	const key = `${item.lane}-${item.row}-${item.layer}`
	if (!rowSpanMap.has(key)) {
		rowSpanMap.set(key, groupedData.filter(d => d.lane === item.lane && d.row === item.row && d.layer === item.layer).length)
	}
})
console.log('Â§ÑÁêÜÂêé groupedData', groupedData)
groupedData.sort((a, b) => {
	if (a.row != b.row) {
		return a.row - b.row // Êåâ row ÂçáÂ∫è
	}
	return b.layer - a.layer // Êåâ layer ÈôçÂ∫è
})
